<template>
  <div class="barChart" ref="Echart" />
</template>

<script>
import echarts from "echarts";
export default {
  props: {
    list: {
      type: Array,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      chart: null,
    };
  },
  computed: {
    lang() {
      return this.$i18n.locale;
    },
  },
  watch: {
    list: {
      handler(list) {
        const xData = [];
        const yData = [];
        if (list && list.length) {
          list.forEach((item) => {
            if (item.date) {
              const day = new Date(item.date * 1000).getDate();
              xData.push(`${day}${this.lang === "tw" ? "日" : "D"}`);
              yData.push(item.dailyVolumeUSD);
            }
          });
        }
        this.xData = xData;
        this.yData = yData;
        this.setOptions();
      },
      deep: true,
      immediate: true,
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.setOptions();
    },
    setOptions() {
      let option = {
        grid: {
          top: "10%",
          left: "10%",
          right: "25%",
          bottom: "15%", //也可设置left和right设置距离来控制图表的大小
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
          },
          formatter: () => {
            return "";
          },
        },
        xAxis: {
          data: this.xData,
          axisLine: {
            show: false, //隐藏X轴轴线
            lineStyle: {
              color: "#01FCE3",
            },
          },
          axisTick: {
            show: false, //隐藏X轴刻度
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: "#666", //X轴文字颜色
            },
          },
        },
        yAxis: [
          {
            type: "value",
            name: "",
            nameTextStyle: {
              color: "#7a6eaa",
            },
            position: "right",
            rigth: 20,
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            axisLabel: {
              show: true,
              formatter: (num) => {
                if (num > 1000000) {
                  return Number(num / 1000000).toFixed(2) + "M";
                }
                return Number(num).toFixed(2);
              }, //右侧Y轴文字显示
              textStyle: {
                color: "#7a6eaa",
              },
            },
          },
          {
            type: "value",
            gridIndex: 0,
            min: 50,
            max: 100,
            splitNumber: 8,
            splitLine: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              show: false,
            },
            splitArea: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "Volume",
            type: "bar",
            barWidth: 2,
            itemStyle: {
              normal: {
                barBorderRadius: [2, 2, 0, 0],
                color: "#31C7D1",
              },
            },
            data: this.yData,
          },
        ],
      };
      if (!this.chart) return;
      this.chart.setOption(option);
      var _this = this;
      this.chart.getZr().off();
      this.chart.getZr().on("click", function (params) {
        let pointInPixel = [params.offsetX, params.offsetY];
        let pointInGrid = _this.chart.convertFromPixel(
          { seriesIndex: 0 },
          pointInPixel
        );
        // x轴数据的索引值
        let value = pointInGrid[1];
        let date = option.xAxis.data[pointInGrid[0]];
        console.log(value);
        _this.$emit("setValue", { date, value });
      });
    },
  },
};
</script>
<style lang="less">
.barChart {
  width: 100%;
  height: 100%;
}
</style>
