<template>
  <div class="barChart" ref="Echart"></div>
</template>

<script>
import * as echarts from "echarts";
export default {
  props: {
    list: {
      type: Array,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      echart: null,
    };
  },
  computed: {
    lang() {
      return this.$i18n.locale;
    },
  },
  watch: {
    list: {
      handler(list) {
        const xData = [];
        const yData = [];
        if (list && list.length) {
          list.forEach((item, index) => {
            if (item.date) {
              const open = Number(
                list[index > 0 ? index - 1 : index].derivedUSD
              );
              const close = Number(item.derivedUSD);
              const lowest = open > close ? close : open;
              const highest = open > close ? open : close;
              xData.push(new Date(item.date * 1000));
              // [开盘值, 收盘值, 最低值, 最高值]
              yData.push({
                value: [open, close, lowest, highest],
              });
            }
          });
        }
        console.log(yData);
        this.xData = xData;
        this.yData = yData;
        this.setOptions();
      },
      deep: true,
      immediate: true,
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
    window.addEventListener(
      "resize",
      this.debounce(() => {
        this.echart && this.echart.resize();
      })
    );
  },
  beforeDestroy() {
    if (this.echart) {
      this.echart.dispose();
      this.echart = null;
    }
  },
  methods: {
    debounce(fn, interval) {
      var timer;
      var gapTime = interval || 150;
      return function () {
        clearTimeout(timer);
        var context = this;
        var args = arguments;
        timer = setTimeout(function () {
          fn.call(context, args);
        }, gapTime);
      };
    },
    initChart() {
      this.echart = echarts.init(this.$el, "macarons");
      this.setOptions();
    },
    setOptions() {
      let option = {
        grid: {
          top: 15,
          left: 15,
          bottom: 15,
          right: 15,
          containLabel: true,
        },
        axisLabel: {
          color: "#877db2",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
          },
          formatter: function (params) {
            return "";
          },
        },
        dataZoom: [
          {
            type: "slider",
            show: false,
            start: 0,
            end: 100,
          },
          {
            type: "inside",
            show: false,
            start: 0,
            end: 100,
          },
        ],
        xAxis: {
          axisLine: {
            show: false,
          },
          splitLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          data: this.xData,
        },
        yAxis: {
          axisLine: {
            show: false,
          },
          splitLine: {
            show: false,
          },
          position: "right",
          axisTick: {
            show: false,
          },
          scale: true,
        },
        series: [
          {
            type: "candlestick",
            data: this.yData,
            // itemStyle: {
            //   color: "#ed4b9e",
            //   color0: "#31d0aa",
            //   borderColor: "#ed4b9e",
            //   borderColor0: "#31d0aa",
            //   borderWidth: 0,
            // },
          },
        ],
      };
      if (!this.echart) return;
      this.echart.setOption(option);
      var _this = this;
      this.echart.getZr().off();
      this.echart.getZr().on("click", function (params) {
        let pointInPixel = [params.offsetX, params.offsetY];
        let pointInGrid = _this.echart.convertFromPixel(
          { seriesIndex: 0 },
          pointInPixel
        );
        // x轴数据的索引值
        let value = pointInGrid[1];
        let date = option.xAxis.data[pointInGrid[0]];
        console.log(value, date);
        _this.$emit("setValue", { date, value });
      });
    },
  },
};
</script>
<style lang="less" scoped>
.barChart {
  width: 100%;
  height: 100%;
}
</style>
